import { sanityClient } from '@/lib/sanity';
import Link from 'next/link';

async function getLatestContent() {
  const news = await sanityClient.fetch(`
    *[_type == "news"] | order(publishedAt desc)[0...6] {
      _id,
      title,
      "slug": slug.current,
      excerpt,
      "image": image.asset->url,
      publishedAt
    }
  `);

  const articles = await sanityClient.fetch(`
    *[_type == "articles"] | order(publishedAt desc)[0...6] {
      _id,
      title,
      "slug": slug.current,
      excerpt,
      "image": image.asset->url,
      publishedAt
    }
  `);

  return { news, articles };
}

export default async function Home() {
  const { news, articles } = await getLatestContent();

  return (
    <main className="container mx-auto px-4 py-8">
      <h1 className="text-5xl font-bold mb-12 text-center">Autamobilismo</h1>

      {/* Seção de Notícias */}
      <section className="mb-12">
        <div className="flex justify-between items-center mb-6">
          <h2 className="text-3xl font-bold">Últimas Notícias</h2>
          <Link href="/news" className="text-blue-600 hover:underline">
            Ver todas →
          </Link>
        </div>
        <div className="grid md:grid-cols-3 gap-6">
          {news.map((item) => (
            <Link 
              key={item._id} 
              href={`/news/${item.slug}`}
              className="border rounded-lg overflow-hidden hover:shadow-lg transition"
            >
              {item.image && (
                <img src={item.image} alt={item.title} className="w-full h-48 object-cover" />
              )}
              <div className="p-4">
                <h3 className="text-xl font-semibold mb-2">{item.title}</h3>
                <p className="text-gray-600 text-sm">{item.excerpt}</p>
              </div>
            </Link>
          ))}
        </div>
      </section>

      {/* Seção de Artigos */}
      <section>
        <div className="flex justify-between items-center mb-6">
          <h2 className="text-3xl font-bold">Últimos Artigos</h2>
          <Link href="/article" className="text-blue-600 hover:underline">
            Ver todos →
          </Link>
        </div>
        <div className="grid md:grid-cols-3 gap-6">
          {articles.map((item) => (
            <Link 
              key={item._id} 
              href={`/article/${item.slug}`}
              className="border rounded-lg overflow-hidden hover:shadow-lg transition"
            >
              {item.image && (
                <img src={item.image} alt={item.title} className="w-full h-48 object-cover" />
              )}
              <div className="p-4">
                <h3 className="text-xl font-semibold mb-2">{item.title}</h3>
                <p className="text-gray-600 text-sm">{item.excerpt}</p>
              </div>
            </Link>
          ))}
        </div>
      </section>
    </main>
  );
}